body {
  background: #fff;
  margin: 0;
}

.page {
  scroll-behavior: smooth;
  overflow: hidden scroll;
}

.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .container {
    max-width: 1280px;
    margin-inline: auto;
    padding-inline: 56px;
  }
}

.page-overlay {
  z-index: 1;
  background-color: #00000080;
  width: 100%;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.menu:target ~ .page-overlay {
  background-color: #00000080;
  display: block;
}

.menu:target ~ .header {
  background-color: #00000080;
}

.page__main {
  --columns: 6;
  column-gap: 30px;
  display: grid;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.header {
  position: relative;
}

.header__top {
  padding-top: 27px;
}

@media (width >= 640px) {
  .header__top {
    padding-top: 45px;
  }
}

.header__menu {
  background-image: url("Museun-menu.040fa19c.webp");
  background-repeat: no-repeat;
  background-size: 18px 12px;
}

.header__bottom {
  align-items: center;
  margin-top: 20px;
  display: flex;
}

@media (width >= 640px) {
  .header__bottom {
    margin-top: 38px;
  }
}

@media (width >= 1280px) {
  .header__bottom {
    margin-top: 89px;
  }
}

.header__title {
  text-transform: uppercase;
  text-align: center;
  color: #0f0e08;
  margin: 269px 0 0;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

@media (width >= 640px) {
  .header__title {
    text-align: left;
    margin-top: 64px;
    font-size: 54px;
    line-height: 64.8px;
  }
}

@media (width >= 1280px) {
  .header__title {
    margin-top: 143px;
    font-size: 72px;
    line-height: 86.4px;
  }
}

.header__button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #1a5a4c;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
  margin-bottom: 40px;
  padding: 0;
  text-decoration: none;
  transition: transform .3s;
  display: flex;
}

.header__button:hover {
  transform: scale(1.05);
  box-shadow: 2px 2px 10px #0003;
}

@media (width >= 640px) {
  .header__button {
    width: 270px;
    height: 70px;
    margin-bottom: 68px;
  }
}

@media (width >= 1280px) {
  .header__button {
    margin-bottom: 279px;
    margin-left: 154px;
  }
}

.header__button-text {
  text-transform: uppercase;
  color: #0f0e08;
  margin: 0 auto;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

.header__button-dec {
  background-color: #1a5a4c;
  background-image: url("Vector.6c348dd9.png");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 7.41px 12px;
  width: 50px;
  height: 50px;
}

.header__button-dec:active {
  background: #1a4e43 url("Vector.6c348dd9.png") 50% / 7.41px 12px no-repeat;
}

@media (width >= 640px) {
  .header__button-dec {
    width: 70px;
    height: 70px;
  }
}

.header__events {
  display: none;
}

@media (width >= 1280px) {
  .header__events {
    text-decoration: none;
    display: inline-block;
    top: 547px;
    left: 61px;
  }
}

.header__events-text {
  color: #687480;
  text-transform: uppercase;
  margin-left: 0;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
}

.header__events-text:before {
  content: "";
  background-color: #1a5a4c;
  width: 70px;
  height: 1.3px;
  margin: 7px 20px 0 -9px;
  display: block;
  position: absolute;
  left: -80px;
}

@media (width >= 1280px) {
  .header__events-text {
    text-align: center;
    transform-origin: 0;
    margin: 0 auto;
    text-decoration: none;
    display: block;
    transform: rotate(270deg)translateX(68px);
  }
}

.header__bg-container {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__bg {
