.menu {
  opacity: 0;
  z-index: 10;
  background-color: #1a5a4c;
  max-height: 0;
  padding-block: 27px;
  transition: max-height .5s, transform .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  transform: translateY(-100%);
}

@media (width >= 640px) {
  .menu {
    padding-top: 40px;
    padding-bottom: 17px;
  }
}

@media (width >= 1280px) {
  .menu {
    padding-bottom: 29px;
  }
}

.menu__wrapper {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  display: grid;
}

@media (width >= 640px) {
  .menu__wrapper {
    --columns: 12;
    grid-template-columns: repeat(var(--columns), 1fr);
    column-gap: 30px;
  }
}

.menu__nav {
  --columns: 3;
  border-bottom: 1px solid #fff;
  grid-column: 1 / -1;
  grid-template-columns: .2fr 3.75fr 3.75fr;
  padding-bottom: 18px;
  display: grid;
}

@media (width >= 640px) {
  .menu__nav {
    border-bottom: none;
    grid-column: 1 / 6;
    padding-right: 60px;
    display: grid;
  }
}

.menu__nav-icon {
  grid-column: 1;
  width: 24px;
  height: 24px;
  transition: transform .3s;
}

.menu__nav-icon:hover {
  transform: scale(1.1);
}

.menu__nav-list {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-column: 2 / -1;
  gap: 24px 28px;
  margin: 0;
  padding-left: 28px;
  list-style-type: none;
  display: grid;
}

@media (width >= 640px) {
  .menu__nav-list {
    gap: 4px 20px;
    padding-bottom: 47px;
    padding-left: 40px;
  }
}

@media (width >= 1280px) {
  .menu__nav-list {
    padding-left: 102px;
  }
}

.menu__nav-item {
  text-align: left;
  margin: 0;
}

.menu__nav-item:hover {
  transition: transform .1s;
  transform: scale(.98);
}

.menu__nav-link {
  color: #fff;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
}

@media (width >= 1280px) {
  .menu__nav-link {
    font-size: 22px;
    line-height: 22px;
  }
}

.menu__schedule {
  color: #fff;
  grid-column: 1 / -1;
  gap: 16px;
  padding-top: 24px;
  display: grid;
}

@media (width >= 640px) {
  .menu__schedule {
    --columns: 2;
    border-left: 1px solid #fff;
    grid-column: 6 / -1;
    grid-template-columns: .7fr .6fr;
    gap: 20px 50px;
    padding-top: 0;
    padding-left: 43px;
    display: grid;
  }
}

@media (width >= 1280px) {
